header {
  background-color: #1E90FF;
  padding: 5px;
  margin: 5px;
  margin-right: 20px;
  border-radius: 10px;
  box-shadow: 5px 5px 5px black;
  width: 99%;
  height: 100px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

#logo {
  height: 80px;
  margin-top: 5px;
  box-shadow: 5px 5px 5px black;
  border-radius: 50%;
  background-color: white;
}

#header-nav {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: flex-end;
  max-width: 600px;
}

.nav-line {
  margin: 0;
  padding: 0;
}

.header-nav-ul {
  list-style: none;
  text-decoration: none;
  margin: 0;
  padding: 0;
  line-height: 0.2;
}

#header-nav li {
  display: inline-block;
  margin-right: 5px;
  list-style: none;
  text-decoration: none;
}

#header-nav button,
#header-nav select {
  vertical-align: middle;
}

#header-nav .nav-tem {
  color: white;
  font-size: 20px;
  font-weight: bold;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  text-shadow: 2px 2px 2px black;
  box-shadow: 5px 5px 5px black;
  border-radius: 20px;
  border: 5px solid #000000;
  background-color: #1E90FF;
  padding: 10px 20px;
  margin: 0px; 
  text-align: center; 
}

#curriculum_id {
  color: white;
  font-size: 10px;
  font-weight: bold;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  text-shadow: 2px 2px 2px black;
  border-radius: 20px;
}

.progress-chart {
  width: 100px;
  margin-top: 5px;
  height: 20px;
  background-color: #ccc;
  border-radius: 10px;
  overflow: hidden;
}

.progress-bar {
  width: 5%;
  height: 10%;
  background-color: #1e90ff;
  transition: width 0.5s ease-in-out;
}

#header-nav .nav-tem:hover {
  background-color: #00BFFF;
  color: black;
  box-shadow: 5px 5px 5px black;
}

#header-nav .nav-tem:active {
  background-color: #00BFFF;
  color: black;
  box-shadow: 5px 5px 5px black;
}


#dashboard-container {

  display: grid;
  width: 99%;
  grid-template-columns: 18% 81.5%;
  gap: 10px;
  margin-top: 120px;
}

#school-topic-window {

  box-shadow: 5px 5px 5px black;
  border-radius: 10px;
  background-color: gray;
  padding: 10px;
  height: 520px;
}

#school {

  height: 150px;
  box-shadow: 5px 5px 5px black;
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 10px;
  margin: 5px;
  text-align: center;
}

#school-title {

  font-size: 20px;
  font-weight: bold;
  font-family: 'Arial', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

#school-list-button {

  border-radius: 5px;
  background-color: #00B7EB;
  color: black;
  font-size: 16px;
  font-weight: bold;
  font-family: Arial, sans-serif;
  box-shadow: 5px 5px 5px black;
  margin: 5px;
  padding: 5px 20px;
}

#school-list-button:hover {

  background-color: black;
  color: white;
  box-shadow: 5px 5px 5px black;
}

#subjects1 {

  height: 260px;
  margin-top: 0px;
  box-shadow: 5px 5px 5px black;
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 10px;
  margin: 10px;
  text-align: center;
}

#subject1 h3 {

  font-size: 20px;
  font-weight: bold;
  font-family: 'Arial', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

#subjects1 {

  list-style-type: none;
  text-align: center;
  left: px;
}

#subjects-li {

  text-align: center;
  margin: 5px;
  padding: 5px;
  border-radius: 5px;
  background-color: #00B7EB;
  color: black;
  font-size: 16px;
  font-weight: bold;
  font-family: Arial, sans-serif;
  box-shadow: 5px 5px 5px black;
}

#subjects-li a:hover {

  /* background-color: white;
  color: white;
  box-shadow: 5px 5px 5px black; */
}

#study-window {

  box-shadow: 5px 5px 5px black;
  border-radius: 10px;
  background-color: white;
  padding: 10px;
  height: 520px;
  right: 10px;
  background-image: url(backgroundimage1.jpg);
  background-repeat: no-repeat;
  background-size: cover;

}

#topicslist {

  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(0, 1fr));
  width: 99%;
  height: 30px;
  box-shadow: 5px 5px 5px white;
  border-radius: 10px;
  background-color: rgb(4, 56, 116);
  padding: 5px;
  margin: px;
  text-align: center;
  font-size: 20px;
  font-weight: bold;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

