#contentdisplay {

  height: 420px;
  width: 100%;
  margin-top: 5px;
  padding: 0px, 5px, 0px, 5px;
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 10px;
  box-shadow: 5px 5px 5px black;
  color: white;
  z-index: 50;
  overflow-y: auto;
}

/* Media queries */
@media screen and (max-width: 1200px) {
  h1,
  .nav-tem {
    font-size: 18px;
  }
}

@media screen and (max-width: 992px) {
  header {
    height: auto;
